/* This is used for the card media queries. Should be refactored elsewhere. */
$tablet: 749px;

.result-card {
  background-color: $white;
  border: 1px solid $light-grey-4;
  border-bottom: 2px solid $light-grey-4;
  margin-bottom: ms(0);

  a {
    color: $primary-text;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &.federated .entry-header {
    background-color: $light-grey-7;
    border-top: 1px solid $light-grey-2;

    .entry-view-type {
      color: $dark-grey-0;
    }
  }

  .entry-header {
    @include row();
    padding: ms(-2);
    border-bottom: 1px solid $light-grey-5;

    .entry-title {
      @include span-columns(9);

      .entry-name {
        font-weight: 400;
        display: inline-block;
        margin-right: ms(-2);
        font-size: ms(2);
      }

      @include media($tablet) {
        @include span-columns(8);

        .entry-name {
          font-size: ms(4);
        }
      }
    }

    .entry-view-type {
      @include span-columns(3);
      font-size: ms(-1);
      text-align: right;
      color: $grey;
      font-weight: 300;
      padding-top: ms(-6);

      @include media($tablet) {
        @include span-columns(4);
        font-size: ms(0);
      }
    }
  }

  .entry-content {
    @include row();

    .entry-main {
      @include media($tablet) {
        @include span-columns(8, block-collapse);
        border-right: 1px solid $light-grey-4;
      }

      .entry-description {
        padding: ms(-2);
        font-size: ms(0);

        .more-link {
          white-space: nowrap;
          color: $light-grey-1;
          text-decoration: underline;

          &:hover {
            color: $light-grey-0;
          }
        }
      }
    }

    .meta-title {
      font-size: ms(-1);
      color: $grey;
      font-weight: 600;
    }

    .entry-meta {
      font-size: ms(-1);

      &.first {
        border-bottom: 1px solid $light-grey-5;
        overflow: hidden;
        padding: ms(-4) ms(-2);

        @include media($tablet) {
          border-bottom: none;
          border-top: 1px solid $light-grey-5;
        }

        .topics {
          @include span-columns(12);

          @include media($tablet) {
            @include span-columns(9);
          }

          .meta-tag-more {
            color: $light-grey-0;
          }
        }

        .api {
          display: none !important;

          @include media($tablet) {
            @include span-columns(3);
            text-align: right;
          }
        }
      }

      &.second {
        @include row();
        padding: ms(-4) ms(-2);
        border-bottom: 1px solid $light-grey-5;

        @include media($tablet) {
          @include span-columns(4);
          border-bottom: none;

          .meta-title {
            padding-top: ms(-7);
          }

          .meta-title,
          .date {
            display: block;
          }
        }

        .updated {
          @include span-columns(6);
          margin-bottom: 0;

          @include media($tablet) {
            @include span-columns(12);
          }
        }

        .views {
          @include span-columns(6);
          text-align: right;

          @include media($tablet) {
            @include span-columns(12);
            text-align: left;

            .meta-title,
            .views {
              display: block;
            }
          }
        }
      }
    }
  }

  .show-wrapper {
    font-size: ms(-1);
    text-align: center;

    a {
      padding: ms(-4) 0;
      display: block;
      background-color: $light-grey-6;

      &:hover {
        text-decoration: none;
        background-color: $light-grey-5;
      }

      [class^="icon-"],
      [class*="icon-"] {
        font-size: ms(-3);
        line-height: 1;
      }
    }
  }

  &.media {
    @include span-columns(12);

    @include media($tablet) {
      @include span-columns(5);
    }

    flex: 0 0 260px;

    .entry-header {
      padding: ms(-8) ms(-5) 0;
      .entry-title {
        @include span-columns(11);
        min-height: 48px;
        .entry-name {
          font-size: ms(0);
        }
      }

      .entry-view-type {
        @include span-columns(1);
        padding-top: 0;
      }
    }

    .entry-meta {
      @include row();
      padding: ms(-9) ms(-5);

      .first {
        @include span-columns(6);
      }

      .second {
        @include span-columns(6);
        text-align: right;
        .tag-category {
          font-size: ms(-2);
          margin-right: 0;
        }
      }

      .date {
        font-size: ms(-2);
      }
    }

    .entry-content {
      .entry-main {
        border-right: none;

        @include media($tablet) {
          @include span-columns(12);
        }

        .entry-description {
          font-size: ms(-1);
          padding: ms(-5);
          min-height: 78px;
        }
      }

      img {
        width: 100%;
        min-height: 136px;
        max-height: 140px;
      }
    }
  }
}

.media-results {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;

  @include media($tablet) {
    @include row();
  }

  &.header {
    @include row();
    border-top: 1px solid $light-grey-3;
    .header-content {
      @include span-columns(12);
      padding: ms(-3) 0 0;
    }
  }

  &.footer {
    @include row();

    .footer-action {
      @include span-columns(12);
      padding: 0 0 ms(1);
      font-size: ms(-1);
    }
  }
}
