@font-face {
  font-family: "socrata-icons";
  src: url(socrata-icons-path("socrata-icons.eot"));
  src: url(socrata-icons-path("socrata-icons.eot?#iefix")) format("eot"),
    url(socrata-icons-path("socrata-icons.woff2")) format("woff2"),
    url(socrata-icons-path("socrata-icons.woff")) format("woff"),
    url(socrata-icons-path("socrata-icons.ttf")) format("truetype"),
    url(socrata-icons-path("socrata-icons.svg#socrata-icons")) format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: "socrata-icons";
    src: url(socrata-icons-path("socrata-icons.svg#socrata-icons")) format("svg");
  }
}
